import { Link, useOutletContext } from "react-router-dom"
import styles from "./Home.module.css"


export function Home() {

  const context = useOutletContext()

  return (

    <div className={styles.main}>

      <h1>D um <i id={styles.arrow} className='bx bx-up-arrow-alt' ></i><span id={styles.arrow}>UP</span></h1>
      <h1>Na sua gesto</h1>
      <div className={styles.lowerTitle}>
        <p>Nosso renomado gerenciador de preos e estoque est aqui para ajudar.</p>
        <div className={styles.buttons}>
          {context.token ? (
            <>
              <Link to="/Dashboard">
                <div className={styles.firstLogged}>Dashboard</div>
              </Link>
              <div className={styles.copy}>
                 2024 Fernando Kohn
                <a target="_Blank" className={styles.gitLink} href="https://github.com/FernandoKohn/t_alpha"><i class='bx bxl-github'></i></a>
              </div>
            </>
          ) : (
            <>
              <Link to="/Login">
                <div className={styles.first}>Login</div>
              </Link>
              <Link to="/Registrar">
                <div className={styles.second}>Registrar</div>
              </Link>
              <div className={styles.copy}>
                 2024 Fernando Kohn
                <a target="_Blank" className={styles.gitLink} href="https://github.com/FernandoKohn/t_alpha"><i class='bx bxl-github'></i></a>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  )
}


